import React, { Component } from "react";
import { Link, animateScroll as scroll } from "react-scroll";
import { Link as RouterLink } from "react-router-dom";

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navOpen: false,
      page: props.page,
    };

    this.navToggle = this.navToggle.bind(this);
  }

  navToggle() {
    this.setState({ navOpen: !this.state.navOpen });
  }

  componentDidMount() {}

  componentWillUnmount() {}

  render() {
    return (
      <div className="navbar-full">
        <div className="container py-3 px-2">
          <div className="row navbar-body py-2">
            <div className="col">
              <p className="font-purple font-800">KetamineMD</p>
            </div>
            <div className="col-auto m-only" onClick={this.navToggle}>
              <img
                className={`mobile-arrow transition-all ${
                  this.state.navOpen ? "upside-down" : ""
                }`}
                fill="white"
                src={require("../media/down-arrow.svg")}
              />
            </div>
            <div
              className={`w-100 m-only nav-mobile-links transition-all ${
                this.state.navOpen ? "nav-expanded" : ""
              }`}
            >
              {this.props.page === "home" ? (
                <Link
                  spy={true}
                  smooth={true}
                  offset={-50}
                  duration={500}
                  className="col-auto"
                  to="about"
                >
                  ABOUT
                </Link>
              ) : (
                <RouterLink className="col-auto" to="/">
                  ABOUT
                </RouterLink>
              )}

              <RouterLink className="col-auto" to="/research">
                RESEARCH
              </RouterLink>
              {this.props.page === "home" ? (
                <Link
                  spy={true}
                  smooth={true}
                  offset={-50}
                  duration={500}
                  className="col-auto"
                  to="treatments"
                >
                  TREATMENTS
                </Link>
              ) : (
                <RouterLink className="col-auto" to="/">
                  TREATMENTS
                </RouterLink>
              )}
              {this.props.page === "home" ? (
                <Link
                  spy={true}
                  smooth={true}
                  offset={-50}
                  duration={500}
                  className="col-auto"
                  to="contact"
                >
                  CONTACT
                </Link>
              ) : (
                <RouterLink className="col-auto" to="/">
                  CONTACT
                </RouterLink>
              )}
            </div>
            <div className="col-auto d-only row">
              {this.props.page === "home" ? (
                <Link
                  spy={true}
                  smooth={true}
                  offset={-50}
                  duration={500}
                  className="col-auto purple-a"
                  to="about"
                >
                  ABOUT
                </Link>
              ) : (
                <RouterLink className="col-auto purple-a" to="/">
                  ABOUT
                </RouterLink>
              )}
              <RouterLink className="col-auto purple-a" to="/research">
                RESEARCH
              </RouterLink>
              {this.props.page === "home" ? (
                <Link
                  spy={true}
                  smooth={true}
                  offset={-50}
                  duration={500}
                  className="col-auto purple-a"
                  to="treatments"
                >
                  TREATMENTS
                </Link>
              ) : (
                <RouterLink className="col-auto purple-a" to="/">
                  TREATMENTS
                </RouterLink>
              )}
              {this.props.page === "home" ? (
                <Link
                  spy={true}
                  smooth={true}
                  offset={-50}
                  duration={500}
                  className="col-auto purple-a"
                  to="contact"
                >
                  CONTACT
                </Link>
              ) : (
                <RouterLink className="col-auto purple-a" to="/">
                  CONTACT
                </RouterLink>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Navbar;
